.palette, .hueScale {
    display: inline-block;
}
.palette {
    position: relative;
    width: 88%;
    height: 88%;
}
.palette > canvas {
    width: 100%;
    height: 100%;
}
.hueScale {
    position: relative;
    margin-left: 2%;
    width: 8%;
    height: 88%;
}
.hueScale > canvas {
    width: 100%;
    height: 100%;
}
.colourSwatch {
    margin-top: 1%;
    height: 8%;
}
.colourSwatch > div {
    float: left;
    width: 8%;
    height: 100%;
}
.colourSwatch > input {
    height: 100%;
    margin-left: 2%;
    font-size: 12pt;
    width: 6em;
    text-align: center;
    color: #373737;
}
.palette > canvas, .hueScale > canvas, .colourSwatch > div, .colourSwatch > input {
    border: 1px #c2c2c2 solid;
    border-radius: 7px;
}
.marker {
    position: absolute;
    top: 0;
    left: 0;
    margin: -10px 2px 2px -10px;
    width: 16px;
    height: 16px;
    border: 3px white solid;
    border-radius: 100%;
}
.hueScale .marker {
    left: 50%;
}